/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

:root {
  --min-content-width: 50ch;
  --max-content-width: 80ch;
  --grid-spacing: 1.33rem;
}

:root {
  --neutral: #2c3e50;
  --accent: #fc4349;
  --accent-complement: #fe6f37;
  --highlight: #ffffff;
  --gray: #121212;
}

/* FADE */

.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity 100ms ease-in-out;
  transition: opacity 100ms ease-in-out;
}

.fade-enter-to, .fade-leave {
  opacity: 1;
}

.fade-enter, .fade-leave-to {
  opacity: 0;
}

/* FADE-OUT */

.fade-out-active, .fade-out-leave-active {
  -webkit-transition: opacity 50ms ease-in-out;
  transition: opacity 50ms ease-in-out;
}

.fade-out-active {
  -webkit-transition-duration: 0ms;
          transition-duration: 0ms;
}

.fade-out-enter-to, .fade-out-leave {
  opacity: 1;
}

.fade-out-enter, .fade-out-leave-to {
  opacity: 0;
}

/* DECK */

.deck-enter-active {
  -webkit-animation: push-in 100ms linear;
          animation: push-in 100ms linear;
}

@-webkit-keyframes push-in {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }

  10% {
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes push-in {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }

  10% {
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.deck-leave-active {
  -webkit-animation: slide-out 100ms linear;
          animation: slide-out 100ms linear;
  z-index: 1;
}

@-webkit-keyframes slide-out {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  10% {
    opacity: 1;
  }

  100%{
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    opacity: 0;
  }
}

@keyframes slide-out {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  10% {
    opacity: 1;
  }

  100%{
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    opacity: 0;
  }
}

/* SLIDE-IN */

.slide-in-enter-active {
  animation: slide reverse 100ms ease-in-out;
}

@-webkit-keyframes slide{
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
}

@keyframes slide{
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
}

a, p {
  line-height: 1.5;
}

h1, h2, h3, h4, h5 {
  line-height: 1.25;
}

h1 {
  text-transform: uppercase;
}

h1, h3 {
  font-weight: bold;
}

h2 {
  color: var(--gray);
}

h3, h4 {
  color: var(--neutral);
}

h2, h4 {
  font-weight: 100;
}

p:not(:first-of-type){
  margin-top: 1em;
}

a, a:link, a:visited{
    text-decoration: none;
    color: inherit;
    position: relative;
}

